.Landing {
  height: 100%;
  padding-top: 62px;
}
.Landing-cover {
  background-image: url('/img/cover.jpg');
  background-size: cover;
  width: 100%;
  height: 700px;
}
.Landing-heading,
.Landing-subtitle {
  color: white;
}
.Landing-getStarted {
  padding: 10px 12px;
}
.Landing-laravelAngular {
  position: relative;
}
.Landing-ampersand {
  font-size: 50px;
  color: #00A4C8;
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  text-align: center;
}
.Landing-angular {
  background-color: #e0e0e5;
}
.Landing-laravel,
.Landing-angular {
  text-align: center;
  color: #21455B;
}
.Landing-laravelAngular-title {
  padding-top: 35px;
}
.Landing-laravelAngular-subtitle {
  color: #757584;
}
.DemoCode {
  overflow: auto !important;
  padding: 10px;
  line-height: 1.428571429;
  color: #333333;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #f5f5f5;
  border: 1px solid #cccccc;
  border-radius: 4px;
  width: 80%;
  margin: 10px auto 30px;
  text-align: left;
  padding-left: 20px;
}
.DemoCode-highlight {
  color: #00A4C8;
}
.DemoCode-comment {
  color: #A8A8B7;
}
.DemoCode-operator {
  color: #757584;
}
.DemoCode-string {
  color: #43A047;
}
.DemoCode-secondary {
  color: #FFC107;
}
.Landing-features {
  text-align: center;
  margin-top: 60px;
}
